import React from "react";

export default function BarCard(props) {
  const [showGif, setShowGif] = React.useState(false);

  return (
    <div className="flex flex-col items-center ">
      <div className="absolute z-0 flex flex-col items-center pt-2 w-24 h-20 sm:w-36">
        <h2 className="text-black text-xl md:text-4xl w-full text-center">
          {props.barName}
        </h2>
        <h4 className="text-sm text-black md:text-sm font-bold text-center w-3/4">
          {props.barDescription}
        </h4>
      </div>
      {props.autoShowIndex === props.index || showGif ? (
        <img
          className="w-full max-w-lg z-20"
          src={props.gifURL}
          alt="FUBAR Battle Bar"
          onMouseOut={() => {
            setShowGif(false);
          }}
        />
      ) : (
        <img
          className="w-full max-w-lg z-20"
          src={props.imageURL}
          alt="FUBAR Battle Bar Still"
          onMouseEnter={() => {
            setShowGif(true);
          }}
        />
      )}
      <a
        className="shadow-2xl my-10 md:my-15 py-2 px-5 rounded-md bg-white border-2 border-black hover:bg-black  hover:border-white text-black hover:text-white"
        href={props.linkURL}
      >
        <h2 className="text-md ">SHOP NOW</h2>
      </a>
    </div>
  );
}

